import React from 'react';

class HotTag extends React.Component {
    render() {
        return (
            <section className="side-block hot-tag">
                <section className="side-block-title">
                    <h6>热点标签</h6>
                </section>
                <ul className="hot-tag-list cl-f">
                    <li>行业</li>
                    <li>观点</li>
                    <li>政策</li>
                    <li>比特币</li>
                    <li>超级账本</li>
                    <li>智能合约</li>
                    <li>中本村</li>
                    <li>去中心化</li>
                    <li>信任机制</li>
                    <li>黑客</li>
                    <li>比特币商业</li>
                    <li>比特币扩容</li>
                    <li>财富自由</li>
                    <li>信用</li>
                </ul>
            </section>
        )
    }
}

export default HotTag;


